<template>
  <div class="wrapper">
    <div class="content" v-for="item in shop" :key="item.id">
      <listItem :shop="item"></listItem>
    </div>
  </div>
</template>

<script>
import listItem from "./list-item";
export default {
  name: 'list-wrapper',
  components: {
    listItem
  },
  props: {
    id: {
      type: String,
      default: () => {
        return ''
      }
    },
    latitude: {
      type: String,
      default: () => {
        return ''
      }
    },
    longitude: {
      type: String,
      default: () => {
        return ''
      }
    }
  },
  data () {
    return {
      shop: [],
      page: 0,
      limit: 10
    }
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll);
  },
  methods: {
    handleScroll () {
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
      let windowHeight = document.documentElement.clientHeight || document.body.clientHeight
      let scrollHeight = document.documentElement.scrollHeight || document.body.scrollHeight;
      if (scrollTop + windowHeight == scrollHeight) {
        this.getVideoDatasByPage(this.page)
      }
    },
    async getVideoDatasByPage (page) {
      try {
        let resShop = await this.$api.WMC.getShopsDatas({
          offset: page,
          limit: this.limit,
          term_id: '6',
          latitude: this.latitude,
          longitude: this.longitude
        })
        if (resShop.data.code === 0) {
          let arr = [...this.shop, ...resShop.data.data]
          arr = arr.filter((x, index) => {
            var arrids = []
            arr.forEach((item) => {
              arrids.push(item.id)
            })
            return arrids.indexOf(x.id) === index
          })
          this.shop = arr
          this.page += this.limit
          return true
        } else {
          return false
        }
      } catch (error) {
        this.$Message.error({
          content: error,
          duration: 4,
          closable: true
        });
      }
    }
  },
  async mounted () {
    window.addEventListener('scroll', this.handleScroll);
    this.getVideoDatasByPage(0)
  }
}
</script>

<style lang="stylus" scoped>
.wrapper {
  .item-right {
    div {
      display: flex;
      align-items: center;
      margin-right: 3%;

      p {
        font-size: 40px;
        color: #fff;
      }

      .address {
        width: 80px;
        height: 80px;
      }
    }
  }

  .content {
    margin: 0 0 40px;
  }
}
</style>
