<template>
  <div>
    <homeHeader></homeHeader>
    <div style="position:relative">
      <videoShow :sources_src="videoUrl" :poster="videoImg"></videoShow>
      <p class="video-des">{{title}}</p>
      <div class="address-content">
        <div>
          <img class="address" src="@/img/address.png">
          <p>{{currentDatas.city}}</p>
        </div>
      </div>
    </div>

    <listWrapper :id="id" :latitude="currentDatas.latitude" :longitude="currentDatas.longitude"></listWrapper>
  </div>
</template>

<script>
import homeHeader from "./components/header";
import listWrapper from "./components/list/list-wrapper";
import videoShow from "./components/list/videoShow";
export default {
  name: 'list',
  components: {
    homeHeader,
    listWrapper,
    videoShow
  },
  data () {
    return {
      video: '',
      title: '',
      id: '',
      currentDatas: {}
    }
  },
  mounted () {
  },

  created () {
    let { videoData, id, title } = this.$route.query;
    videoData = JSON.parse(videoData)
    if (sessionStorage.getItem('currentPositioning')) {
      this.currentDatas = JSON.parse(sessionStorage.getItem('currentPositioning')).currentPositioning
    }
    this.videoUrl = videoData.url
    // this.videoUrl = 'http://v5-dy-a.ixigua.com/9752f6d144dcf0e485220833f7bf332a/5e730dea/video/tos/cn/tos-cn-ve-15/d34197f014bc4946b4c5e0b78b75d55f/?a=1128&br=0&bt=1404&cr=0&cs=0&dr=0&ds=3&er=&l=20200319131452010012021016161ED800&lr=aweme&qs=0&rc=ajU0M3E7O2dzcTMzNWkzM0ApO2g2ZTM3aWVlNzdoZDY6aGdwMG9faTJkcmxfLS1fLS9zc2JeLzJhYjFeNjBfXl80NC06Yw%3D%3D&vl=&vr='
    this.videoImg = videoData.cover
    this.title = title
    this.id = id
  },
}
</script>

<style>
body {
  background: #161824;
}
.video-des {
  font-size: 40px;
  font-weight: bold;
  color: #fff;
  width: 70%;
  margin-top: 10px;
  word-wrap: break-word;
  word-break: normal;
  padding-bottom: 80px;
}
.address-content {
  position: absolute;
  right: 4%;
  bottom: 0;
  white-space: nowrap;
}
.address-content div {
  display: flex;
  align-items: center;
  margin-right: 3%;
}
.address-content div p {
  font-size: 30px;
  color: #fff;
}
.address-content div .address {
  width: 80px;
  height: 80px;
}
</style>